#blog {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 640px;
    margin: 20px auto;
}

#header {
    display: block;
    padding: 20px 0;
}

#header-image {
    height: 360px;
    object-fit: contain;
    object-position: center;
}

#title, .h1, .h2, .p {
    color: var(--title);
}

#title {
    font-size: 40px;
}

.h1, .h2 {
    display: block;
    margin: 20px 0 8px 0;
}

.h1 {
    font-size: 28px;
    margin: 20px 0 8px 0;
}

.h2 {
    font-size: 24px;
}

.p {
    font-size: 18px;
    line-height: 1.5em;
    margin: 16px 0;
}

.a {
    color: var(--link);
    text-decoration: none;
    padding: 2px 6px;
    border-radius: 4px;
    transition: background-color .15s ease-out;
}

.a:hover,
.a:focus {
    background-color: var(--link-overlay);
}

.figure, .img {
    display: block;
    width: 100%;
    border-radius: 6px;
}

.figure {
    margin: 20px 0;
    background-color: var(--preload);
    box-shadow: 0 8px 28px rgba(0,0,0,.25);
    transition: box-shadow .2s ease-out;
}

.figure:hover,
.figure:focus {
    margin: 20px 0;
    box-shadow: 0 12px 60px rgba(0,0,0,.325);
}

.img {
    margin: 0;
    min-height: 100%;
}

/* Nav */
#more-story {
    width: 880px;
    color: var(--title);
    font-size: 36px;
    margin: 20px auto 12px auto;
    padding: 0 20px;
}

#nav {
    display: flex;
    flex-direction: row;
    width: 880px;
    margin: 0 auto 60px auto;
}

#previous,
#next {
    display: flex;
    flex: 1;
    flex-direction: column;
    margin: 0 20px;
    text-decoration: none;
}

#previous > .h2,
#next > .h2 {
    text-align: center;
    font-size: 21px;
    font-weight: 400;
    margin: 0;
}

.preload-story {
    object-fit: cover;
    object-position: center;
    height: 225px;
}

@media (max-width: 767.9px) {
    #blog, #more-story, #nav {
        width: 90%;
    }

    .preload-story {
        height: calc(25vw - 20px)
    }

    #header-image {
        height: 50.63vw;
    }
}

@media (max-width: 567.9px) {
    #nav {
        flex-direction: column;
    }

    #next, #previous {
        margin-bottom: 20px;
    }

    .preload-story {
        height: calc(50vw - 20px);
    }
}

@media (min-width: 768px) and (max-width: 965.9px) {
    #nav, #more-story {
        width: 760px;
    }

    .preload-story {
        height: 190.81px
    }
}